<template>
    <div class="pl-4 pr-4 async-content-fixed sub-header" v-if="loading">
        
        <v-icon class="circle-around d-inline-block" size="14">mdi-loading</v-icon>
        <div class="fs-12 d-inline-block">同步中...</div>
    </div>

    <span v-else></span>
</template>

<script>
    export default {
        name: '',
        props: {
            unid: {
                type: String 
            }
        },
        data() {
            return {
                _unid: null,
                loading: false,

                me_sponser: null,
                me_enroller: null
            }
        },
        mounted() {
            
            setTimeout(() => {
                this.syncData()
            }, 4000);
        },
        methods: {
            async syncData() {

                this.loading = true
                if (this.unid) {
                    this._unid = this.unid 
                } else {
                    this._unid = this.$store.state.user_backend.unid 
                }

                // this._bonus = await this._bonusLoading()
                this._switchMe()

                this.asyncMe()   
                
                setTimeout(() => {
                    this.loading = false
                }, 2400)
            },
            _switchMe() {
                const _this = this
                const tf =  this.$store.state.transfer
                let many = this.$store.state.users_sponser

                this.bonus._build(many, tf, function(u, t) {
                    if (u.unid == _this._unid) {
                        _this.me_sponser = u
                    }
                })

                many = this.$store.state.users_enroller
                this.bonus._build(many, tf, function(u, t) {
                    if (u.unid == _this._unid) {
                        _this.me_enroller = u
                    }
                })
            },
            async _bonusLoading() {
                let res = {
                    'chronu': this.$store.state.chronus.id,
                    '_sort': 'published_at:DESC',
                    'user.unid': this._unid
                }

                return await this.conn.get(this.api.bonus, this.$store.state.token, res)
            },
            async _bonusUpdate(data) {
                const res = await this.conn.put(
                    this.api.bonus + '/' + data.id,
                    this.$store.state.token,
                    data
                )
            },
            asyncMe() {
                const user = this.me_sponser
                const data = {
                    ...user.bonus, 
                    vip: user.vip.name + '_' + user.vip.star,
                    son_can_num_sponser: user.transfer.son_can_num_SPONSER,
                    son_can_num_enroller: user.transfer.son_can_num_ENROLLER,
                    son_pv_sponser: user.transfer.son_pv_SPONSER + '',
                    son_pv_enroller: user.transfer.son_pv_ENROLLER + ''
                }
                data.usd_recommend = this.me_enroller.bonus.usd_recommend

                this._bonusUpdate(data)
            }
        },
    }
</script>

<style lang="sass" scoped>
    
.async-content-fixed
    position: absolute
    bottom: 0
    z-index: 999
</style>


